body {
    padding: 20px;
    background-color: white;
    color: black;
    font-family: 'Arial';
    text-align: center;
    font-size: 20px;
}

img {
    border-radius: 50%;
    padding: 5px;
    margin: 5px;
}

button {
    font-family: "Arial";
    border: none;
    color: black;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    display: inline-block; 
    background-color: lightcyan;
    transition-duration: 0.3s;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    margin-right: 5px;
    margin-left: 5px;
    margin-top: 5px;
    margin-bottom: 5px;
}

button:hover {
    background-color: lightseagreen;
    color: white;
}


#header {
    display: flex;
    margin-bottom: 10px;
    align-items: center;
}

.alignleft {
    width: 30%;
    text-align: left;
    float: left;
}

.aligncenter {
    width: 40%;
    text-align: center;
    font-size: 30px;
}

.alignright {
    width: 30%;
    text-align: right;
    float: right;
}

#post {
    text-align: left;
    margin: 10px;
}
